import { Component, OnInit } from '@angular/core';
import { FormBuilder,FormGroup,Validators } from '@angular/forms';
import  {ReactiveFormsModule} from '@angular/forms';
import {Router,ActivatedRoute}  from '@angular/router';
import {OperationService}  from '../operation.service';

@Component({
  selector: 'app-couponsedit',
  templateUrl: './couponsedit.component.html',
  styleUrls: ['./couponsedit.component.scss']
})
export class CouponseditComponent implements OnInit {
  coupon:FormGroup;
    x: any;
    model: any = {};
    submitted = false;
    id:any;
    title:string;
  constructor(private fb : FormBuilder,private active:ActivatedRoute,private operation:OperationService,private router:Router) { }


update(){
      
     //insert and update coupon

      if(this.id==undefined){

        this.submitted = true;
        if (this.coupon.invalid) {
           
            return;
        }else{
       this.operation.insertdata(this.coupon.value,"coupon_reg")
       this.router.navigate(['coupons'])
            }

      }else{
         this.submitted = true;

        // stop here if form is invalid
        if (this.coupon.invalid) {
           
            return;
        }else{
          this.operation.updatedata(this.coupon.value,'coupon_update');
          
          this.router.navigate(['coupons']);
        }
        
        console.log(this.coupon.invalid)
        alert('SUCCESS!! :-)')
   
        
      }
}





  ngOnInit() {

this.active.params.subscribe(params=>{
       this.id=params['id'];
       

         if(this.id==undefined){
              this.title="Add coupon";
           }
           else{
             this.title="Update coupon"
             this.operation.Edit(params['id'],"editcoupon").subscribe(res=>{
           this.x=res[0];
             // put values in the form
             console.log(res)
      this.coupon.setValue({
      coupon_id:this.x.coupon_id ,
      code:this.x.code ,
      reward:this.x.reward,
      type:this.x.type ,
      expires_at:this.x.expires_at ,


                });
         })
           }
  });



        this.coupon=this.fb.group(
    {
       
coupon_id:[''],
code:['',[Validators.required]],
reward:['',[Validators.required]],
type:['',[Validators.required]],
expires_at:['',[Validators.required]],


    });
  }

   get f() { return this.coupon.controls; }




}
